// common mixins - start

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
    @include eltdfRelativeHolderLayout(top);
    margin: 35px 0 0;
    clear: both;

    @media all and (max-width: 1024px){
        margin: 35px 0 53px;
    }

    ul {
        @include eltdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 6px;

            a,
            >span{
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 6px;
                font-family: $label-text-font;
                font-size: 19px;
                font-weight: 400;
                line-height: 1.47em;
                letter-spacing: 0;
            }

            &.eltdf-pag-active {
                
                a {
                    color: $first-main-color;
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
                margin: 0 2px;

                a {
                    font-size: 19px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }

                        svg{
                            height: 11px;
                            margin-top: 9px;

                            polyline{
                                fill: transparent;
                                stroke: currentColor;
                            }
                        }
                    }
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next{
                margin: 0 2px;
            }

            @if ($list_type == 'shortcode') {
                
                &.eltdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.eltdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 19px;
                            line-height: 26px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $additional-font;
    font-size: 13px;
    line-height: 2em;
    font-weight: 600;
    letter-spacing: .07em;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin eltdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 7px 37px;

    } @else if ($size == 'medium') {
        padding: 8px 43px 10px;

    } @else if ($size == 'large') {
        padding: 10.5px 53px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin eltdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $third-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
    color: $third-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin eltdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
    font-family: inherit;
    font-size: 16px;
    font-weight: inherit;
    line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
    color: $default-heading-color;
    background-color: transparent;
    border-top:none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid $input-border-color;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include eltdfPlaceholder {
        font-family: $label-text-font;
        font-size: 16px;
        font-weight: 300;
        line-height: 1.62em;
        letter-spacing: 0;
        color: $default-text-color;
        background-color: $default-background-color;
        -webkit-font-smoothing: antialiased;
    }
}

// mixins styles - end

