// heading mixins - start

@mixin eltdfH1() {
    font-family: $default-heading-font;
    font-size: 51px;
    line-height: 1.15em;
    letter-spacing: .16em;
}

@mixin eltdfH2() {
    font-family: $default-heading-font;
    font-size: 40px;
    line-height: 1.17em;
    letter-spacing: .16em;
}

@mixin eltdfH3() {
    font-family: $default-heading-font;
    font-size: 32px;
    line-height: 1.25em;
    letter-spacing: .16em;
}

@mixin eltdfH4() {
    font-family: $default-heading-font;
    font-size: 27px;
    line-height: 1.33em;
    letter-spacing: .16em;
}

@mixin eltdfH5() {
    font-family: $default-heading-font;
    font-size: 22px;
    line-height: 1.31em;
    letter-spacing: .16em;
    font-weight: 400;
}

@mixin eltdfH6() {
    font-family: $default-heading-font;
    font-size: 20px;
    line-height: 1.35em;
    letter-spacing: .16em;
    font-weight: 400;
}

@mixin title(){
    font-family: $subtitle-text-font;
    font-size: 21px;
    font-style: italic;
    font-weight: 300;
    line-height: 1.47em;
    letter-spacing: 0;
    color: $first-main-color;

    >a{
        color: $first-main-color;
    }
}

@mixin subtitle(){
    font-family: $label-text-font;
    font-size: 19px;
    font-weight: 400;
    line-height: 1.47em;
    letter-spacing: 0;
    color: $default-text-color;
}

@mixin readmoreStyle(){
    font-size: 13px;
    line-height: 2.15em;
    letter-spacing: .07em;
    font-family: $additional-font;
    color: $read-more-text-color;

    a {
        text-transform: none;
        font-size: 13px;
        line-height: 2.15em;
        letter-spacing: .07em;
        font-weight: 600;
        font-family: $additional-font;
        color: $read-more-text-color;

        &:before{
            content: "";
            width: 34px;
            display: inline-block;
            height: 1px;
            background-color: $first-main-color;
            margin-bottom: .25em;
            margin-right: 10px;
            transform: scaleX(0);
            transform-origin: 0 0;
            transition: .3s;
        }

        &:after{
            content: "";
            width: 34px;
            display: inline-block;
            height: 1px;
            position: absolute;
            top: 50%;
            left: 0;
            background-color: $first-main-color;
            margin-bottom: .25em;
            margin-right: 10px;
            transform: scaleX(1);
            transform-origin: 100% 0;
            transition: .3s .2s;
        }

        &:hover {
            &:before {
                transform: scaleX(1);
                transition: .3s .2s;
            }

            &:after {
                transform: scaleX(0);
                transition: .3s;
            }
        }
    }
}

@mixin readmoreStyleHover{

    &:before{
        content: "";
        width: 0;
        height: 0;
        display: inline-block;
        background-color: $first-main-color;
        margin-bottom: .25em;
        transition: width .2s ease-in-out;
    }
    &:hover{
        color: initial;
    }
}

@mixin eltdfHeadingStyle($with-heading: null, $with-color: true) {

    @if ($with-color) {
        color: $third-main-color;
    }

    @if ($with-heading == 'h1') {
        @include eltdfH1();
    } @else if ($with-heading == 'h2') {
        @include eltdfH2();
    } @else if ($with-heading == 'h3') {
        @include eltdfH3();
    } @else if ($with-heading == 'h4') {
        @include eltdfH4();
    } @else if ($with-heading == 'h5') {
        @include eltdfH5();
    } @else if ($with-heading == 'h6') {
        @include eltdfH6();
    }
}

@mixin eltdfDefaultHeadingStyle() {
    @include eltdfHeadingStyle();
    font-family: $default-heading-font;
    margin: 25px 0;
    font-weight: 400;
    text-transform: uppercase;
    color: $third-main-color;

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include eltdfTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin eltdfBody() {
    font-family: $label-text-font;
    font-size: 17px;
    font-weight: 400;
    line-height: 1.58em;
    letter-spacing: 0;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

// heading mixins - end

// other mixins start

@mixin eltdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin eltdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin eltdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin eltdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin eltdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

// IE browser media query

@mixin ie-media-query{
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        @content;
    }
}

@mixin edge-media-query{
    @supports (-ms-ime-align:auto) {
        @content;
    }
}

